.filterIconContainer {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 40px;
    height: 36px;

    background: #FFFFFF;
    border: 1px solid #E8E8E8;
    border-radius: 2px;
}

.filterIconContainer:hover {
    background-color: lightgray;
}

.filterIcon {
    cursor: pointer;
}

.filterIconDisabledContainer {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 40px;
    height: 36px;

    background: #FFFFFF;
    border: 1px solid #E8E8E8;
    border-radius: 2px;
    pointer-events: none;
    opacity: 0.5;
}
